<template>
<v-container >
  <h2>Enter Movie Details</h2><br>
  <v-form ref="MovieForm">
     <v-row>
          <v-col cols="6"><v-text-field v-model="name" label="Movie Name*" :rules="[v => !!v || 'Please Enter Movie Name']" ></v-text-field></v-col>
    <v-col cols="6"><v-text-field v-model="yearOfRelease" label="Year of Release*" type="date"  :rules="[v => !!v || 'Please Enter Year Of Release']" ></v-text-field></v-col>
        </v-row>
        <v-container>
         <v-row>
      <v-flex xs12 md10>
        <v-select v-model="newActors" :items="actorNames" label="Actors*" multiple :rules="[(v) => (v && v.length > 0) || 'Please Select Actors']"></v-select>
      </v-flex>
      <v-flex xs12 md2>
        <v-btn block dark @click="openPersonModal('Actor')" type="button">Add Actor</v-btn>
      </v-flex>
    </v-row>
        </v-container>
        <v-container>
    <v-row>
      <v-flex xs12 md10>
        <v-select v-model="newProducer"  :items="producerNames" label="Producer*" :rules="[v => !!v || 'Please Select Producer']"></v-select>
      </v-flex>
      <v-flex xs12 md2>
        <v-btn block dark @click="openPersonModal('Producer')" type="button">Add Producer</v-btn>
      </v-flex>
    </v-row>
        </v-container>
    <div data-app><v-dialog v-model="dialog" max-width="600">
    <add-person 
  :entity="entity"
 @addActor="addActor"
 @addProducer="addProducer"
  @close="closeDialog"
  ></add-person>
  </v-dialog>
    </div>
    <v-select v-model="genre" :items="genreNames" label="Genre*" multiple :rules="[(v) => (v && v.length > 0) || 'Please Select Genre']" ></v-select>
    <v-text-field v-model="language" label="Language*" :rules="[v => !!v || 'Please Enter Language']"></v-text-field>
    <v-textarea auto-grow rows="1" v-model="plot" label="Plot*" :rules="[v => !!v || 'Please Enter Plot']" ></v-textarea>
    <v-file-input v-model="poster" label="Poster*" :rules="[v => !!v || 'Please Upload Poster']"></v-file-input>
    <v-row>
      <v-col cols="6"><v-btn block dark><router-link :to="'/movies'">Back</router-link></v-btn></v-col>
    <v-col cols="6"><v-btn block dark @click="addMovie()">{{ isEditMode ? 'Update Movie' : 'Add Movie' }}</v-btn></v-col>
    </v-row>
  </v-form>
  
</v-container>
</template>

<script>
import AddPerson from '@/components/AddPerson.vue'
import { mapActions,mapGetters } from 'vuex';
export default {
  components:{
    AddPerson,
  },
  created(){
   
    this.fetchActors();
    this.fetchProducers();
    this.fetchGenres();

}, 
  data() {
    return {    
        name: '',
        yearOfRelease: '',
        plot: '',
        language:'',
        genre:[],
        poster:null,
        isPersonModalOpen: false, 
        newActors:[], 
        newProducer:'', 
        dialog:false,
        entity:'',
        
    };
  },

mounted() {
  if (this.isEditMode) {
   const movieId = this.$route.params.id;
  
     const selectedMovie = this.movies.find(movie => {
  return movie.id == movieId;
});
if(selectedMovie){
  
        this.name = selectedMovie.name;       
        this.yearOfRelease=`${selectedMovie.yearOfRelease}-01-01`;
        this.plot=selectedMovie.plot;
        this.language=selectedMovie.language;
        this.newProducer=selectedMovie.producer;
        this.genre=selectedMovie.genre.map(genre=>genre.name);
        this.newActors=selectedMovie.actors.map(actor=>actor.name);
        
}
  }},
  
    computed:{
       isEditMode() {
    return !!this.$route.params.id;
  },
  ...mapGetters({
      movies: 'movies/getAllMovies',
      actors: 'actors/getAllActors',
      producers: 'producers/getAllProducers',
      genres: 'genres/getAllGenres',
    }),
     genreNames() {
      return this.genres.map(genre => genre.name);
    },
      actorNames() {
      return this.actors.map(actor => actor.name);
    },
    
     producerNames() {
      return this.producers.map(producer => producer.name);
    }
   
   
  },
  
  methods: {
    
  ...mapActions({
    fetchActors: 'actors/fetchActors',
    fetchProducers: 'producers/fetchProducers',
    fetchGenres: 'genres/fetchGenres',
  }),
  openPersonModal(entity) {
      this.entity = entity;
      this.dialog = true;
    },
     closeDialog(){
      this.dialog=false;
    },
    addMovie() {
     
        if(this.$refs.MovieForm.validate()){
          
          const actorIds = this.newActors.map(actorName => {
      const actor = this.actors.find(actor => actor.name === actorName);
      return actor.id;
    });

      const genreIds = this.genre.map(genreName => {
      const genre = this.genres.find(genre => genre.name === genreName);
      return genre.id;
    });

    const movie = {
      name: this.name,
      yearOfRelease: parseInt(this.yearOfRelease.substring(0,4)),
      plot: this.plot,
      language:this.language,
      poster: this.poster,
      producer: this.newProducer,
      actorIds: actorIds,
      genreIds: genreIds
    };
    if(this.isEditMode){
      const movieId = this.$route.params.id;
      this.$store.dispatch(`movies/updateMovie`, { id: movieId, movie });
    }
    else{
 
          this.$store.dispatch("movies/addMovie",movie);
    }
          
        }

    },

  addActor(actor) {
       this.$store.dispatch('actors/addActor', actor).then(() => {
       
        this.$store.dispatch('actors/fetchActors')
      });
      this.newActors.push(actor.name);
        
        this.dialog = false;
    },
  addProducer(producer) {
   this.$store.dispatch('producers/addProducer', producer).then(() => {
       
        this.$store.dispatch('producers/fetchProducers')
      });
        this.dialog = false;
        this.newProducer=producer.name;
}
  },
};
</script>
<style scoped>
  ::v-deep .error--text {
    color: red; 
  }
  a{
    text-decoration: none;
    color:white;
  }
 
</style>

