import router from '@/router.js';
export default{
    namespaced:true,
    state:{
        movies:[],
        favoriteMovies:[]
        
    },
    getters:{
        getAllMovies(state){
            return state.movies;
        },
        getFavoriteMovies(state){
          return state.favoriteMovies;
        }
    },
    mutations:{
     
      setMovies(state, payload) {
        state.movies = payload;
        
      },
      deleteMovie(state, movieId) {
        const index = state.movies.findIndex(movie => movie.id === movieId);
        if (index !== -1) {
          state.movies.splice(index, 1);
        }
      },
      addToFavorite(state,movie){
        state.favoriteMovies.push(movie);
        const index = state.movies.findIndex(m => m.id === movie.id);
        if (index !== -1) {
          state.movies.splice(index, 1);
        }
      }
    
     
  },
    actions: {
      async addMovie(context, movie) {
        const formData = new FormData();
        formData.append('file', movie.poster);
        console.log(movie.poster);
        const imageResponse = await fetch('https://localhost:44380/movies/upload', {
          method: 'POST',
          body: formData,
        });
        const imageUrl = await imageResponse.text();
    
        const movieRequest = {
          name: movie.name,
          yearOfRelease: movie.yearOfRelease,
          plot: movie.plot,
          language:movie.language,
          coverImage: imageUrl,
          producer: movie.producer,
          actors: movie.actorIds,
          genre: movie.genreIds,
        };
        console.log(JSON.stringify(movieRequest));
        const response = await fetch('https://localhost:44380/movies', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(movieRequest),
        });
    
        if (response.ok) {
          router.push('/movies');
        }
      },
      async updateMovie(context, { id, movie }) {
        const formData = new FormData();
        formData.append('file', movie.poster);
        console.log(movie.poster);
        const imageResponse = await fetch('https://localhost:44380/movies/upload', {
          method: 'POST',
          body: formData,
        });
        const imageUrl = await imageResponse.text();
    
        const movieRequest = {
          name: movie.name,
          yearOfRelease: movie.yearOfRelease,
          plot: movie.plot,
          language:movie.language,
          coverImage: imageUrl,
          producer: movie.producer,
          actors: movie.actorIds,
          genre: movie.genreIds,
        };
        console.log(JSON.stringify(movieRequest));
        const response = await fetch(`https://localhost:44380/movies/${id}`, {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(movieRequest),
        });
    
        if (response.ok) {
          
          
          router.push('/movies');
        }
      },

      addToFavorite(context,movie){
          context.commit('addToFavorite',movie);
      },
      async fetchMovies(context) {
        
        const response = await fetch('https://localhost:44380/movies');
        if (response.ok) {
          const movies = await response.json();
          context.commit('setMovies', movies);
        } else {
          console.log('Failed to fetch movies.');
        }
      },
      
      async deleteMovie(context,id) {
       
        const response = await fetch(`https://localhost:44380/movies/${id}`,{
          method:'DELETE'
        });
        if (response.ok) {
         
          context.commit('deleteMovie', id);
        } else {
          console.log('Failed to delete movies.'+response);
        }
     
    }
      
    },
    
}