<template>
  <v-card class="movie-card">
    <v-img :src="movie.coverImage" height="200"></v-img>
    <v-card-title class="title">
      <strong>{{ movie.name }}</strong>
    </v-card-title>
    <v-card-text style="overflow-y: hidden; height:106px">| {{movie.yearOfRelease}} | {{movie.language}}<br>{{ movie.plot }}</v-card-text>
    <v-card-text>
    <span>Actors |<span v-for="actor in movie.actors" :key="actor.name">
      {{ actor.name }} |
    </span></span><br>
    <span>Producer | {{movie.producer}}</span>
    </v-card-text>
    <v-card-actions>
      <v-btn @click="exploreMovie(movie)" text>Explore <span class="material-symbols-outlined">arrow_right_alt</span></v-btn>
      <v-spacer></v-spacer>
       <v-btn @click="addToFavorite(movie)" text><span class="material-symbols-outlined">Favorite</span></v-btn>
      <v-btn @click="editMovie(movie)" text><span class="material-symbols-outlined">edit_square</span></v-btn>
      <v-btn @click="deleteMovie(movie)" text><span class="material-symbols-outlined">delete</span></v-btn>
    </v-card-actions>
  </v-card>
  
</template>

<script>

export default {

  props: {
    movie: {
      type: Object,
      required: true,
    },
    
  
  },
 
  
  methods: {
    exploreMovie(movie) {
      this.$emit('explore', movie);
    },
    editMovie(movie) {
      this.$emit('edit', movie);
    },
    deleteMovie(movie) {
      this.$emit('delete', movie);
    },
    addToFavorite(movie){
      this.$emit('addToFavorite',movie);
    }
  },
};
</script>

<style scoped>
.movie-card {
  margin-bottom: 20px;
 
}
.title {
  font-weight: bold;
}
a{
  text-decoration: none;

}
v-icon{
  fill:none;
}

</style>
