
export default{
    namespaced:true,
    state:{
        actors:[]
    },
    getters:{
        getAllActors(state){
            return state.actors;
        }
    },
    mutations:{
      addActor(state,payload)
      {
          state.actors.push(payload);
        
      },
      setActors(state, payload) {
        state.actors = payload;
      }
    },
    actions:{
      async addActor(context,actor){
        const formData = new FormData();
        formData.append('file', actor.image);
        const imageResponse = await fetch('https://localhost:44380/actors/upload', {
          method: 'POST',
          body: formData,
        });
        const imageUrl = await imageResponse.text();
        const actorRequest={
          Name:actor.name,
          Gender:actor.gender,
          Image:imageUrl,
          DOB:actor.DOB,
          Bio:actor.about
        };
        console.log(JSON.stringify(actorRequest));
        const response= await fetch('https://localhost:44380/actors',{
          method:'POST',
          headers:{
            'Content-Type':'application/json'
          },
          body:JSON.stringify(actorRequest)
        });
        if(response.ok){
          const addedActor = await response.json();
          context.commit('addActor',addedActor);
          
        }
       
      },
      async fetchActors(context) {
       
          const response = await fetch('https://localhost:44380/actors');
          if (response.ok) {
            const actors = await response.json();
            context.commit('setActors', actors);
          } else {
            console.log('Failed to fetch actors.'+response);
          }
       
      }
      
      
    },
    
}