import Vue from 'vue'
import VueRouter from 'vue-router'

import MovieListingPage from './pages/movies/MovieListingPage.vue'
import MovieCreationPage from './pages/movies/MovieCreationPage.vue'

import ActorListingPage from './pages/actors/ActorListingPage.vue'
import ProducerListingPage from './pages/producers/ProducerListingPage.vue'

import NotFound from './pages/NotFound.vue'

Vue.use(VueRouter);
const router=new VueRouter({
    mode:'history',
    routes:[
        { path:'/', redirect:'/movies'},
        { path:'/movies', component : MovieListingPage },
        { path:'/movies/editMovie/:id', component : MovieCreationPage },
        { path:'/movies/addMovie', component : MovieCreationPage },  
        
        { path:'/actors', component : ActorListingPage },
        { path:'/producers', component : ProducerListingPage },

        { path:'/:NotFound(.*)', component : NotFound },
    ],

});
export default router;