<template>
      <v-card >
        <v-card-title>
          <h4>Enter {{ entity }} Details</h4><v-spacer></v-spacer><span> <v-btn class="text" @click="closeDialog" text>&times;</v-btn></span>
        </v-card-title>
        <v-card-text>
          <v-form ref="PersonForm">
        <v-row>
          <v-col cols="12" sm="6"><v-text-field v-model="personName" :label="entity+' Name*'" :rules="[v => !!v || 'Please Enter Name']"></v-text-field></v-col>
          <v-col cols="12" sm="6"><v-text-field v-model="personDOB" type="date" label="DOB*" :rules="[v => !!v || 'Please Enter DOB']"></v-text-field></v-col>
        </v-row>
        <v-row>
          <v-col cols="12" sm="6">
        <v-radio-group row v-model="personGender" label="Gender*" :rules="[v => !!v || 'Please Select Gender']">
          <v-radio label="Male" value="Male"></v-radio>
          <v-radio label="Female" value="Female"></v-radio>
          
        </v-radio-group>
        </v-col>
        <v-col cols="12" sm="6"><v-file-input v-model="personImage" :label="entity+' Image*'" :rules="[v => !!v || 'Please Upload Image']"></v-file-input></v-col>
        </v-row>
        <v-textarea auto-grow rows="1" v-model="personAbout" :label="'About '+entity+'*'" :rules="[v => !!v || 'Please Enter About '+entity]"></v-textarea>
        <br>
        <v-row><v-spacer></v-spacer>
          <v-btn @click="closeDialog" text >Close</v-btn>
          <v-btn @click="addPerson" text>Add {{entity}}</v-btn>
        </v-row>
          </v-form>
       </v-card-text>
      </v-card>

</template>

<script>
export default {
  
  props: {
    entity: {
      type: String,
      required: true,
    },
   
  },
  data() {
    return {
      personName: '',
      personGender: null,
      personImage:null,
      personAbout: '',
      personDOB:null,
      
      dialog: false,
    
    };
  },
 

  methods: {
   
     closeDialog() {
      this.$emit('close');
    },
    addPerson() {
       if(this.$refs.PersonForm.validate()){
         
      if (this.entity === 'Actor') {
        
        const actor={
          name:this.personName,
          gender:this.personGender,
          image:this.personImage,
          about:this.personAbout,
          DOB:this.personDOB
        }
       this.$emit('addActor',actor );
      
      } 
      else if (this.entity === 'Producer') {
         
        const producer={
          name:this.personName,
          gender:this.personGender,
          image:this.personImage,
          about:this.personAbout,
          DOB:this.personDOB
        }
        this.$emit('addProducer', producer);
          
      }
    
      this.personName = '';
      this.personGender= null;
      this.personImage=null;
      this.personAbout= '';
      this.personDOB=null;
     
    }
    },
  },
};
</script>
<style scoped>
.text{
  font-size:25px;
  color:black;
}
</style>