<template>
  <v-container fluid>
<v-row>
  <v-col
          v-for="producer in producers"
          :key="producer.name"
          cols="12"
          sm="3"
          md="2"
          lg="2"
        >
    <v-card>
      <v-img :src="producer.image" class="image"></v-img>
      <v-card-title class="title names">
        {{ producer.name }}
      </v-card-title>
    </v-card>
  </v-col>
  </v-row>
  </v-container>
</template>

<script>

export default {
    created(){
   
    this.$store.dispatch('producers/fetchProducers');
   
}, 
computed:{
      
    producers(){
      return this.$store.getters['producers/getAllProducers'];
    },
}
};
</script>
<style scoped>
.names{
  font-size:15px;
}
.image{
  height:240px;
}
@media only screen and (max-width: 600px) {
 .image{
  height:350px;
 }
}
</style>