
export default{
    namespaced:true,
    state:{
        genres:[]
    },
    getters:{
        getAllGenres(state){
            return state.genres;
        }

    },
    mutations:{
        setGenres(state, payload) {
          state.genres = payload;
        }
      },
      actions:{
        async fetchGenres(context) {
         
            const response = await fetch('https://localhost:44380/genres');
            if (response.ok) {
              const genres = await response.json();
              context.commit('setGenres', genres);
            } else {
              console.log('Failed to fetch genres.');
            }
         
        }

    }
    
}