<template>
    <v-container fluid>
      
       <v-btn dark><router-link :to="'/movies/addMovie'">Add Movie</router-link></v-btn>
        <h4>Favorite Movies</h4>
         <v-row>
        <v-col
          v-for="movie in $store.getters['movies/getFavoriteMovies']"

          :key="movie.name"
          cols="12"
          sm="6"
          md="4"
          lg="3"
        >
          <movie-card
          
            :movie="movie"
            @explore="exploreMovie"
            @edit="editMovie"
            @delete="deleteMovie"
            @addToFavorite="addToFavarite"
          ></movie-card>
        </v-col>
      </v-row>
       <h4>Movies</h4>
      <v-row>
        <v-col
          v-for="movie in $store.getters['movies/getAllMovies']"

          :key="movie.name"
          cols="12"
          sm="6"
          md="4"
          lg="3"
        >
          <movie-card
          
            :movie="movie"
            @explore="exploreMovie"
            @edit="editMovie"
            @delete="deleteMovie"
            @addToFavorite="addToFavorite"
          ></movie-card>
        </v-col>
      </v-row>
     
    <div data-app><v-dialog v-model="dialog" max-width="800">
       <movie-dialog 
       :selectedMovie="selectedMovie"
       :selectedProducer="selectedProducer"
       @close="closeDialog"
       ></movie-dialog>
      </v-dialog>
    </div>
  </v-container>
</template>
    

<script>
import MovieCard from '@/components/MovieCard.vue';
import MovieDialog from '@/components/MovieDialog.vue';
import { mapActions} from 'vuex';

export default {
  components: {
    MovieCard,
    MovieDialog,
  },
   created(){

    this.fetchMovies();
    this.fetchProducers();

},

    data() {
    return {
      movie:{
        name:'',
        plot:'',
        coverImage:''
      },
      dialog: false,
      selectedMovie: {
        name:'',
        plot:'',
        coverImage:'',
        yearOfRelease:'',
        language:''
      },
      selectedProducer:{}
    };
  },
 computed:{
    favoriteMovies(){
      return this.$store.getters['movies/getFavoriteMovies'];
    }
 }, 
methods: {
  ...mapActions({
    fetchMovies: 'movies/fetchMovies',
    fetchProducers: 'producers/fetchProducers',
   
  }),
    exploreMovie(movie) {
      this.selectedMovie=movie;
      const producers= this.$store.getters['producers/getAllProducers'];
      this.selectedProducer = producers.find(producer => {
  return producer.name == movie.producer;
});
      this.dialog = true;
    },
    closeDialog(){
      this.dialog=false;
    },
    editMovie(movie) {
      this.$router.push('/movies/editMovie/' + movie.id);
    },
    deleteMovie(movie) {
      if (confirm('Are you sure you want to delete this movie?')) {
         this.$store.dispatch("movies/deleteMovie",movie.id);
      }
    },
    addToFavorite(movie){
      this.$store.dispatch('movies/addToFavorite',movie);
    }
  },
};

</script>

<style scoped>
.movie-card {
  margin-top: 20px;
  margin-bottom: 10px;
}
.title {
  font-weight: bold;
}
a{
  text-decoration: none;
  color:white;
}
</style> 

