<template>
  <v-container fluid>
<v-row>
  <v-col
          v-for="actor in actors"
          :key="actor.name"
          cols="12"
          sm="3"
          md="2"
          lg="2"
        >
    <v-card>
      <v-img :src="actor.image" class="image"></v-img>
      <v-card-title class="title names">
       {{ actor.name }}
      </v-card-title>
    </v-card>
  </v-col>
  </v-row>
  </v-container>
</template>

<script>

export default {
    created(){
   
    this.$store.dispatch('actors/fetchActors');
   
}, 
computed:{
      
    actors(){
      return this.$store.getters['actors/getAllActors'];
    },
}
};
</script>
<style scoped>
.names{
  font-size:15px;
}
.image{
  height:240px;
}
@media only screen and (max-width: 600px) {
 .image{
  height:350px;
 }
}
</style>