
export default{
    namespaced:true,
    state:{
        producers:[]
    },
    getters:{
        getAllProducers(state){
            return state.producers;
        }
    },
    mutations:{
      addProducer(state,payload)
      {
          state.producers.push(payload);
          
      },
      setProducers(state, payload) {
        state.producers = payload;
      }
    },
    actions:{
      async addProducer(context,producer){
        const formData = new FormData();
        formData.append('file', producer.image);
        const imageResponse = await fetch('https://localhost:44380/producers/upload', {
          method: 'POST',
          body: formData,
        });
        const imageUrl = await imageResponse.text();
        const producerRequest={
          Name:producer.name,
          Gender:producer.gender,
          Image:imageUrl,
          DOB:producer.DOB,
          Bio:producer.about
        };
        console.log(JSON.stringify(producerRequest));
        const response= await fetch('https://localhost:44380/producers',{
          method:'POST',
          headers:{
            'Content-Type':'application/json'
          },
          body:JSON.stringify(producerRequest)
        });
        if(response.ok){
          const addedProducer = await response.json();
          context.commit('addProducer',addedProducer);
          
        }
       
      },
      async fetchProducers(context) {
       
        const response = await fetch('https://localhost:44380/producers');
        if (response.ok) {
          const producers = await response.json();
          context.commit('setProducers', producers);
        } else {
          console.log('Failed to fetch producers.');
        }
     
    }
    
    },
    
}